<template>
	<div class="search">
		<div class="search_bar">
			<!-- <img src="../assets/images/player_title.png" alt="" /> -->
			<!-- 搜索歌曲 -->
			<input type="text" autocomplete="off" v-model="query" @keyup.enter="searchMusic" />
			<a href="" @click.prevent="logout()">退出</a>	
		</div>
		<!-- 搜索歌曲列表 -->
		<div class='song_wrapper'>
			<ul class="song_list">
				<li v-for="item in musicList">
					<a href="javascript:;" @click="playMusic(item.id)"></a>
					<b>{{ item.name }}</b>
					<span v-if="item.mvid!=0" @click="playMV(item.mvid)"><i></i></span>
				</li>
			</ul>
			<div class="audio_con">
				<audio ref='audio' @play="play" @pause="pause" :src="musicUrl" controls autoplay loop class="myaudio"></audio>
			</div>
		</div>
		<div class="video_con" v-show="isShow" style="display: none;">
			<video :src="mvUrl" controls="controls"></video>
			<div class="mask" @click="hide"></div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				// 查询关键字
				query: "",
				// 歌曲数组
				musicList: [],
				artistList: [],
				// 歌曲地址
				musicUrl: "",
				// 动画播放状态
				isPlaying: false,
				// 遮罩层的显示状态
				isShow: false,
				// mv地址
				mvUrl: ""
			}
		},
		methods: {
			// 歌曲搜索
			searchMusic: function() {
				var that = this;
				that.$http.get("https://autumnfish.cn/search?keywords=" + this.query).then(function(response) {
					// console.log(response);
					that.musicList = response.data.result.songs;
					console.log(response.data.result.songs);
					// that.artistList = response.data.result.songs.artistlists;
					// console.log(response.data.result.songs.artistlists);
				}, function(err) {});
			},
			// 歌曲播放
			playMusic: function(musicId) {
				//   console.log(musicId);
				var that = this;
				// 获取歌曲地址
				that.$http.get("https://autumnfish.cn/song/url?id=" + musicId).then(function(response) {
					// console.log(response);
					// console.log(response.data.data[0].url);
					that.musicUrl = response.data.data[0].url;
				}, function(err) {});
			
			},
			// 歌曲播放
			play: function() {
				// console.log("play");
				this.isPlaying = true;
			},
			pause: function() {
				// console.log("pause");
				this.isPlaying = false;
			},
			// 播放mv
			playMV: function(mvid) {
				var that = this;
				that.$http.get("https://autumnfish.cn/mv/url?id=" + mvid).then(function(response) {
					// console.log(response);
					console.log(response.data.data.url);
					that.isShow = true;
					that.mvUrl = response.data.data.url;
					
				}, function(err) {});
			},
			// 隐藏
			hide: function() {
				this.isShow = false;
			},
			logout() {
				this.$store.dispatch('logout')
				this.$router.push('/login')
			},
		}
	}
</script>
<style>
	body,
	ul,
	li,
	dl,
	dd {
		margin: 0px;
		padding: 0px;
	}

	.search {
		margin-top: 100px;
		width: 80%;
		height: 600px;
		margin: 0 auto;
		/*border: 1px solid red;*/
	}

	.search_bar {
		/* padding-top: 100px; */
		width: 60%;
		background-color: #ffffff;
		margin: 0 auto;
		height: 60px;
		/*border: 1px solid red;*/
		border-top-left-radius: 4px;
		border-top-right-radius: 4px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		position: relative;
		z-index: 11;
	}

	.search_bar img {
		margin-left: 23px;
	}

	.search_bar input {
		margin: 0 auto;
		/* margin-right: 23px; */
		width: 60%;
		height: 34px;
		border-radius: 17px;
		border: 0px;
		background: url("../assets/images/zoom.png") 400px center no-repeat rgba(241, 243, 244, 0.5);
		text-indent: 15px;
		outline: none;
	}

	.song_wrapper {
		margin-top: 100px;
		margin-left: 350px;
		/* margin: 0 auto; */
		width: 800px;
		height: 435px;
		box-sizing: border-box;
		padding: 10px;
		list-style: none;
		position: absolute;
		left: 0px;
		top: 0px;
		z-index: 1;
		/*border: 1px solid red;*/
	}

	.song_list {
		width: 100%;
		overflow-y: auto;
		overflow-x: hidden;
		height: 100%;
	}

	.song_list::-webkit-scrollbar {
		display: none;
	}

	.song_list ul {
		width: 800px;
	}

	.song_list li {
		font-size: 12px;
		color: #333;
		height: 40px;
		/* display: flex;
		flex-wrap: wrap; */
		align-items: center;
		width: 800px;
		/* padding-left: 10px; */
		/*border: 1px solid blue;*/
	}

	.song_list li ul {
		display: inline-block;
		float: left;
		/* margin-bottom: 40px; */
	}

	.song_list li ul li {
		margin-top: -20px;
		display: inline-block;
		float: left;
		/*border: 1px solid red;*/
		font-size: 12px;
		color: #333;
		height: 40px;
		/* display: flex;
		flex-wrap: wrap; */
		align-items: center;
	}

	.song_list li:nth-child(odd) {
		/* background-color: rgba(240, 240, 240, 0.3); */
	}

	.song_list li a {
		display: block;
		width: 17px;
		height: 17px;
		background-image: url("../assets/images/play.png");
		background-size: 100%;
		margin-right: 5px;
		box-sizing: border-box;
	}

	.song_list li b {
		font-weight: normal;
		/* width: 122px; */
		width: 300px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	.audio_con {
		width: 100%;
	}

	.myaudio {
		width: 100%;
	}
</style>
