<template>
	<div id="login">
		<div id="title">
			<h1>用户登录</h1>
		</div>
		<div class="input-item">
			<input type="text" placeholder="请输入用户名" v-model="user.username">> </div>
		<div class="input-item">
			<input type="text" placeholder="请输入密码" v-model="user.password">
		</div>
		<div id="butt">
			<button @click="login()"> 立即登录 </button>
		</div>
	</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				user: {
					username: "",
					password: "",
					realname: "",
					user_id: 0,
					infoLists: [],
				},
			}
		},
		methods: {
			// login() {
			// 	this.$store.dispatch('login', {
			// 		username: 'lisi'
			// 	})
			// 	this.$router.push('/memo')
			// }
			login() {
				var params = {
					username: this.user.username,
					password: this.user.password,
					realname: this.user.realname,
					user_id: this.user.user_id,
				};
				var that = this;
				this.$http.post( 'http://teach.yiibook.top/api/v1/user/login', params).then(response => {
					var result = response.data;
					// console.log(result);
					if (result.status == 200) {
						this.$store.dispatch('login', {
							username: result.data.username,
							realname: result.data.realname,
							user_id: result.data.id
						})
						this.$router.push('/search');
					} else {
						alert(result.message);
					}
				}).catch(error => {
					console.log(error);
				});
			},
			// logout() {
			// 	window.localStorage.removeItem('username');
			// 	this.$router.push('/login');
			// },
		}
	}
</script>
<style>
	* {
		padding: 0;
		margin: 0;
	}

	#login {
		width: 400px;
		margin: 0 auto;
		margin-top: 100px;
		/* border: 1px solid  red ; */
	}

	.error-info {
		color: red;
		padding: 20px 0px 0px;
		margin: 0 auto;
	}

	#title {
		width: 400px;
		height: 50px;
		margin: 0 auto;
		/* border: 1px solid  red ; */
	}

	#title h1 {
		text-align: center;
	}

	.input-item {
		width: 400px;
		height: 50px;
		margin: 0 auto;
	}

	.input-item input {
		width: 400px;
		height: 40px;
		margin: 0 auto;
		/* border: 1px solid  green ; */
		text-align: center;
	}

	#butt {
		width: 400px;
		height: 50px;
		line-height: 50px;
		margin: 0 auto;
		margin: 0 auto;
	}

	#butt button {
		width: 400px;
		height: 30px;
		line-height: 30px;
		/* margin-left: 400px; */
		font-size: 18px;
		background-color: aquamarine;
		margin: 0 auto;
	}

	#butt a {
		text-decoration: none;
	}
</style>
